html {
  scroll-behavior: smooth;
  background-color: var(--main-background-color);
}

.wrapper {
  margin: 0 auto;
  width: 90%;
  max-width: 1000px;
  overflow: hidden;
}
.footer {
  width: 100%;
  height: 50px;
  line-height: 50px;
  background-color: var(--nav-color);
  text-align: center;
  color: white;
}

section {
  overflow: hidden;
}

:root {
  --nav-height: 50px;

  --main-color: #2e94cf;
  --nav-color: #222222;
  --grey-color: #969696;
  --light-grey-color: #646668;
  --shadow-light-color: rgba(0, 0, 0, 0.2);
  --shadow-dark-color: rgba(255, 255, 255, 0.247);
  --main-background-color: rgb(12, 12, 12);
  --dark-border-color: rgb(252, 252, 252);
}

:root.lightMode {
  --grey-color: #45474a;
  --shadow-light-color: rgba(255, 255, 255, 0.247);
  --shadow-dark-color: rgba(0, 0, 0, 0.2);
  --main-background-color: rgb(252, 252, 252);
  --dark-border-color: rgb(12, 12, 12);
}

@media (min-width: 1024px) {
  html {
    font-size: 20px;
  }
}
