.switcher {
  position: relative;
  top: 50%;
  left: 5%;
  transform: translateY(-50%);
  height: 20px;
  width: 50px;
  border: 2px solid rgb(252, 252, 252);
  border-radius: 10px;
  background-color: var(--nav-color);
}

.switcher:focus {
  outline: none;
}

.switcherDot {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: var(--main-color);
  cursor: pointer;
}

@media (min-width: 768px) {
  .switcher {
    left: 80%;
    z-index: 1;
  }
}
