.skillsContainer {
  margin: 20px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.skillsText {
  text-align: center;
  font-size: 1rem;
  color: var(--light-grey-color);
}

.skill {
  margin: 10px 0;
  text-align: center;
  width: 30%;
  color: var(--main-color);
}

.skillName {
  color: var(--grey-color);
}

@media (min-width: 768px) {
  .skill {
    width: 18%;
  }
}
