.header {
  height: 100vh;
  background-color: #111111;
  display: flex;
  flex-wrap: wrap;
}

.headerText {
  width: 100%;
  align-self: flex-end;
  text-align: center;
  color: white;
  overflow: hidden;
}

.headerTitle {
  width: 100%;
  text-align: center;
  font-size: 2.8rem;
  font-weight: bold;
  letter-spacing: normal;
}

.headerSubTitle {
  width: 100%;
  text-align: center;
  font-size: 1.5rem;
  font-weight: bold;
  letter-spacing: normal;
}

.headerKeyboard {
  margin: 0 auto 20px;
  display: flex;
  flex-wrap: wrap;
  align-self: flex-end;
  width: 99%;
  max-height: 250px;
  max-width: 350px;
  border: 2px solid white;
  border-radius: 5px;
  color: white;
  box-shadow: 3px 3px 0px 3px rgba(255, 255, 255, 0.2);
}

.keyboardRow {
  margin: 3px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
}
.keyboardLetter {
  width: 30px;
  height: 35px;
  line-height: 30px;
  text-align: center;
  border: 2px solid white;
  border-radius: 5px;
  box-shadow: 2px 2px 0px 2px rgba(255, 255, 255, 0.2);
  transition: 0.2s;
}

@media (min-width: 768px) {
  .headerKeyboard {
    max-width: 480px;
  }
  .keyboardLetter {
    margin: 2px;
    width: 40px;
    height: 45px;
    line-height: 45px;
    user-select: none;
  }
}

.headerKeyboard
  .keyboardRow:nth-last-child(1)
  .keyboardLetter:nth-last-child(5) {
  flex-grow: 1;
}
