.propertyContainer {
  margin: 20px auto;
  width: 50%;
  text-align: center;
}

.propertySquare {
  margin: 20px auto;
  position: relative;
  width: 80px;
  height: 80px;
  background-color: var(--main-color);
  transform: rotate(45deg);
  transition: 0.2s;
}

.propertyIcon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  color: #ffffff;
}
.propertyText {
  margin: 8px 0;
  color: var(--grey-color);
  font-weight: bold;
}

.propertyDescription {
  font-size: 0.7rem;
  color: var(--light-grey-color);
}

@media (min-width: 768px) {
  .propertyContainer {
    width: 22%;
  }
}
