.form {
  position: relative;
  margin: 5px auto;
  padding: 5px;
  height: 100%;
  width: 100%;
  max-width: 700px;

  border-radius: 5px;
  text-align: right;
  background-color: var(--main-color);
}

.formLabel {
  display: block;
  margin: 5px 0;
  width: 100%;
}

.formInput {
  padding: 2px;
  border: none;
  width: 100%;
  font-size: 1.2rem;
  background-color: transparent;
  border-bottom: 2px solid var(--main-background-color);
  color: white;
  transition: 0.3s;
}
.formInput:focus {
  outline: none;
}
.formInput::placeholder {
  color: rgb(243, 243, 243);
}

.formLabel textarea {
  width: 100%;
  height: 100%;
  min-height: 100px;
  border: none;
  resize: none;
}

.formButton {
  margin: 5px;
  padding: 5px 10px;
  border: 2px solid var(--main-background-color);
  border-radius: 5px;
  box-shadow: 3px 3px 0px 1px var(--shadow-light-color);
  background-color: var(--main-background-color);
  color: var(--main-color);
  font-size: 1rem;
  font-weight: bold;
  transition: 0.2s;
  cursor: pointer;
}

.formButton:hover {
  box-shadow: none;
  transform: translate(3px, 3px);
}

.formButton:focus {
  outline: none;
}

.formSuccess {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--main-color);
}
.formSuccessText {
  position: absolute;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.4rem;
  font-weight: bold;
  text-align: center;
  color: white;
}
