.aboutMeContainer {
  margin: 20px 0;
  display: flex;
  flex-wrap: wrap;
}

.aboutMePropertiesContainer {
  display: flex;
  flex-wrap: wrap;
  align-items: space-around;
  justify-content: space-around;
}
.aboutMeContactAndInfoContainer {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  width: 100%;
}

.aboutMeImage {
  margin: 0 15px;
  align-self: center;
  width: 100%;
  max-width: 250px;
  max-height: 250px;
  text-align: right;
  border-radius: 50%;
}

.aboutMeInfo {
  margin: 8px;
  width: 100%;
  font-size: 2rem;
  color: var(--grey-color);
  font-weight: bold;
}

.aboutMeDescrpiton {
  color: var(--light-grey-color);
}

@media (min-width: 768px) {
  .aboutMeInfoContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 45%;
  }
}
