.project {
  position: relative;
  width: 100%;
  z-index: 1;
  color: var(--grey-color);
}

.projectContainer {
  margin: 10px auto;
  padding: 10px 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 300px;
  text-align: center;
  z-index: 2;
}

.projectName {
  width: 100%;
  margin: 10px;
}

.projectLinks {
  display: flex;
  margin: 15px;
  width: 90%;
  justify-content: space-around;
  align-items: center;
}

.projectDescription {
  margin: 10px;
  width: 100%;
}
.projectTechnology {
  width: 100%;
}

.weatherApp {
  background: linear-gradient(#1b2632, #6d9bcd);
  color: white;
}
.sportregeneracja {
  background-position: center;
  background-size: cover;
  color: white;
}
.swiezaskibka {
  background-position: center;
  background-size: cover;
}

.squareGame {
  background-size: cover;
  color: white;
}
