.contactSectionContainer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.contactContainer {
  margin: 50px auto;
  display: flex;
  width: 100%;
  max-width: 460px;
  justify-content: space-around;
  align-items: center;
}

.contactItem {
  margin: 10px 0;
  width: 30px;
  text-align: left;
  color: var(--main-color);
  cursor: pointer;
}

@media (min-width: 768px) {
  .contactItem {
    width: 40px;
  }
}
